.navbar {
  justify-content: space-between;
  margin-bottom: 10px;
  background: rgb(255, 100, 0);
  color: rgb(255, 255, 255);
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.navbar-brand {
  text-transform: uppercase;
  margin: 0px 0px 0px 10px;
}

.logo {
  display: flex;
  align-items: center;
}
